main.adminProductsContainer[data-page^='/admin/products'] {
  min-height: fit-content;
}

.adminProductsContainer .adsStateTab {
  height: 28px;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  padding: 4px;
  margin-left: 25px;
  margin-right: 25px;
  background-color: rgba(5, 190, 192, 0.08) !important;
  color: #323232 !important;
  cursor: pointer;
  font-size: 20px;
  cursor: pointer;
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}

.adminProductsContainer .nav-link.active .adsStateTab,
.adminProductsContainer .adsStateTab.active {
  background-color: #05BEC0 !important;
  color: #ffffff !important;
}

.adminProductsContainer span.viewMore {
  display: inline;
  padding: 0;
  color: #F28F17;
  text-decoration: underline;
  text-align: end;
}


.adminProductsContainer .MuiTableCell-stickyHeader {
  top: 66px;
}

.adminProductsContainer .MuiTableCell-root.MuiTableCell-footer.MuiTablePagination-root {

}
